/* Animations */
@keyframes fade_down {
  0% {
    opacity: 0.01;
    transform: translateY(-20px);
    transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
      transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
    transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
      transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  }
}

@keyframes fade_up {
  0% {
    opacity: 0.01;
    transform: translateY(20px);
    transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
      transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
    transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
      transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  }
}

/* ===================== HEADER BASE ===================== */

.header {
  width: 100%;
  max-width: 100%;
  padding: 16px 20px;
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

/* LOGO */
.header .logo {
  display: flex;
  font-size: 1.6em;
  text-decoration: none;
  align-items: center;
}

/* MENU UL */
.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* MENU ITEMS */
.header li a {
  display: block;
  padding: 14px 20px;
  font-size: 1rem;
  text-decoration: none;
}

/* MENU TOGGLE (default hidden menu) */
.header .menu {
  overflow: hidden;
  max-height: 0;
  width: 100%;
  transition: max-height 0.3s ease;
}

/* HAMBURGER ICON */
.header .menu-icon {
  display: inline-block;
  cursor: pointer;
  padding: 16px;
}

.header .menu-icon .navicon {
  background: #333;
  width: 24px;
  height: 3px;
  position: relative;
  transition: all 0.3s ease;
}

.header .menu-icon .navicon::before,
.header .menu-icon .navicon::after {
  content: "";
  background: #333;
  width: 24px;
  height: 3px;
  position: absolute;
  left: 0;
  transition: all 0.3s ease;
}

.header .menu-icon .navicon::before {
  top: -7px;
}

.header .menu-icon .navicon::after {
  top: 7px;
}

/* HIDDEN CHECKBOX */
.header .menu-btn {
  display: none;
}

/* WHEN MOBILE MENU OPENS */
.header .menu-btn:checked ~ .menu {
  max-height: 400px;
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .navicon::before {
  transform: rotate(-45deg);
  top: 0;
}

.header .menu-btn:checked ~ .menu-icon .navicon::after {
  transform: rotate(45deg);
  top: 0;
}

/* ===================== DESKTOP VIEW (min-width: 768px) ===================== */

@media (min-width: 768px) {
  .menu {
    max-height: none !important;
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
  }

  .header .menu-icon {
    display: none;
  }

  .header li {
    float: left;
  }

  .header {
    padding: 18px 40px;
  }
}

