/* --- NEW: Import creative font from Google Fonts --- */

.raw_logo {
  height: 305;
  width: 440;
}

.myHexagon {
  /* Using the 850 dasharray for the larger hexagons */
  stroke-dasharray: 850;
  stroke-dashoffset: 850;
  animation: dash 2.5s linear forwards;
}

.letter {
  /* --- NEW: Apply the imported font --- */
  font-family: 'Lobster', cursive;
  
  /* This animation fades in the "PM" text */
  opacity: 0;
  animation: fadein 2s linear forwards 2.5s;
}

/* Keyframe for the hexagon line-drawing animation */

@keyframes dash {
  from {
    stroke-dashoffset: 850;
  }
  to {
    stroke-dashoffset: 0;
  }
}

/* Keyframe for the letter fade-in animation */

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
