.opensource-main {
  width: 100%;
}

/* ================== CONTACT PAGE BASE ================== */

.contact-main {
  width: 100%;
  background: var(--themeBackground);
}

.basic-contact {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px 80px;
}

/* ========== MAIN SECTIONS (DESKTOP) ========== */

.contact-heading-div,
.blog-heading-div,
.address-heading-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 40px;
  gap: 40px;
  margin-bottom: 80px;
}

.contact-heading-div > *,
.blog-heading-div > *,
.address-heading-div > * {
  flex: 1 1;
}

/* ========= LEFT IMAGE BLOCKS ========= */

.contact-heading-img-div,
.blog-heading-img-div,
.address-heading-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Top circular profile image */
.contact-heading-img-div > img {
  width: 320px;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  object-fit: cover;
}

/* SVG / image in blog & address sections */
.blog-heading-img-div svg,
.blog-heading-img-div img,
.address-heading-img-div svg,
.address-heading-img-div img {
  max-width: 100%;
  height: auto;
}

/* ========= TEXT BLOCKS ========= */

.contact-heading-text-div,
.blog-heading-text-div,
.address-heading-text-div {
  text-align: left;
}

/* Headings */
.contact-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  margin-top: 0;
  margin-bottom: 20px;
}

.blog-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  margin-top: 0;
  margin-bottom: 20px;
}

.address-heading-text {
  font-size: 30px;
  font-family: "Google Sans Regular";
  margin-bottom: 10px;
  margin-top: 0;
  text-align: center;
}

/* Detail text */
.contact-header-detail-text,
.blog-header-detail-text {
  font-size: 18px;
  line-height: 1.6;
  margin: 0 0 24px 0;
  font-family: "Google Sans Medium";
}

/* Buttons centered horizontally */
.resume-btn-div,
.blogsite-btn-div,
.address-btn-div {
  width: 100%;
  display: flex;
  justify-content: center;
}

.address-btn-div {
  margin-top: 10px;
}

/* ========= CONTACT CARD (RIGHT SIDE) ========= */

.contact-card {
  flex: 1 1;
  padding: 32px 28px;
  border-radius: 25px;
  box-shadow: 0 6px 30px rgba(0,0,0,0.08);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}

/* TITLE */
.contact-title {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 10px;
}

/* SUBTITLE / DESCRIPTION */
.contact-subtitle {
  font-size: 16px;
  margin-bottom: 26px;
  line-height: 1.6;
}

/* CONTACT ROW ITEMS */
.connect-item {
  display: flex;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  margin-bottom: 20px;
}

/* Round icon background */
.icon-circle {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 20px;
  flex-shrink: 0;
}

/* Brand Colors */
.email-icon {
  background: #D44638;
}

.phone-icon {
  background: #34A853;
}

.address-icon {
  background: #EA4335;
}

.linkedin-bg {
  background-color: #0077b5;
}

.github-bg {
  background-color: #24292e;
}

/* LABEL + VALUE */
.connect-label {
  font-size: 1rem;
  font-weight: 600;
}

.connect-value {
  font-size: 0.95rem;
  text-decoration: none;
}

/* MAP BUTTON */
.map-btn {
  display: block;
  text-align: center;
  padding: 12px 20px;
  margin-top: 25px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  transition: 0.3s ease;
}

.map-btn:hover {
  opacity: 0.85;
  transform: translateY(-2px);
}

/* ================== TESTIMONIAL / REACH OUT (BOTTOM) ================== */

.end-contact-wrapper {
  width: 100%;
  background: #f6fbff;
  padding: 0;
  margin: 0;
  position: relative;
}

.connect-testimonial-grid {
  max-width: 980px;
  margin: 0 auto 48px auto;
  padding: 36px 20px 18px 20px;
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
}

.reach-out-section {
  background: #f8fafc;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(108, 99, 255, 0.08);
  padding: 26px 24px 32px 24px;
  flex: 2 1;
  min-width: 280px;
}

.reach-out-title {
  font-size: 2.05rem;
  font-weight: 700;
  color: #df4663;
  margin-bottom: 8px;
  letter-spacing: .1rem;
}

.reach-out-desc {
  color: #5c6c87;
  font-size: 1.13rem;
  margin-bottom: 30px;
  line-height: 1.6;
}

.reach-out-cards {
  display: flex;
  grid-gap: 18px;
  gap: 18px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.reach-out-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(44,48,67,.07);
  padding: 18px 24px;
  display: flex;
  align-items: center;
  grid-gap: 13px;
  gap: 13px;
  min-width: 190px;
  margin: 0 7px 15px 0;
}

.reach-out-icon {
  font-size: 2.2rem;
}

.reach-out-label {
  font-size: 1.06rem;
  font-weight: 600;
  margin-bottom: 2px;
  color: #1a2235;
}

.reach-out-link {
  font-size: 0.97rem;
  color: #df4663;
  word-break: break-all;
  text-decoration: none;
  transition: color .16s;
}

.reach-out-link:hover {
  color: #af264b;
}

/* Testimonial Card */
.testimonial-end-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 14px rgba(44,48,67,.08);
  padding: 30px 28px 22px 28px;
  min-width: 290px;
  margin: 0 7px 15px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.testimonial-quote {
  font-size: 1.05rem;
  color: #35405b;
  line-height: 1.7;
  margin-bottom: 14px;
  font-style: italic;
}

.quote-mark {
  font-size: 2.4rem;
  color: #df4663;
  font-weight: 900;
  margin-right: 9px;
  margin-bottom: -8px;
  vertical-align: middle;
  display: inline-block;
}

.testimonial-author {
  font-size: 0.95rem;
  font-weight: 600;
  color: #6c63ff;
  margin-top: 14px;
  text-align: right;
  width: 100%;
  padding-left: 10px;
}

/* ============ RESPONSIVE FOR TABLETS ============ */

@media (max-width: 1024px) {
  .contact-heading-text,
  .blog-heading-text {
    font-size: 42px;
  }

  .contact-heading-img-div > img {
    width: 260px;
  }
}

/* ============ RESPONSIVE FOR MOBILE ============ */

@media (max-width: 768px) {
  .basic-contact {
    padding: 40px 16px 60px;
  }

  /* Stack sections vertically */
  .contact-heading-div,
  .blog-heading-div,
  .address-heading-div {
    flex-direction: column;
    align-items: center;
    text-align: center;
    grid-gap: 24px;
    gap: 24px;
    margin-bottom: 50px;
  }

  /* Top sections order: for blog -> text first, image after */
  .blog-heading-div {
    flex-direction: column-reverse;
  }

  /* Circular profile image smaller */
  .contact-heading-img-div > img {
    width: 150px;
    height: 150px;
  }

  /* Blog & address SVGs smaller */
  .blog-heading-img-div svg,
  .blog-heading-img-div img,
  .address-heading-img-div svg,
  .address-heading-img-div img {
    width: 75%;
    max-width: 260px;
    height: auto;
    margin: 0 auto;
  }

  .contact-heading-text,
  .blog-heading-text {
    font-size: 30px;
    margin-bottom: 12px;
  }

  .address-heading-text {
    font-size: 22px;
  }

  .contact-header-detail-text,
  .blog-header-detail-text {
    font-size: 15px;
    margin: 0 4px 18px 4px;
    line-height: 1.5;
    text-align: center;
  }

  .contact-heading-text-div,
  .blog-heading-text-div,
  .address-heading-text-div {
    text-align: center;
  }

  /* Contact card full width and tighter padding */
  .contact-card {
    width: 100%;
    padding: 22px 18px;
    box-shadow: 0 4px 20px rgba(0,0,0,0.08);
  }

  .contact-title {
    font-size: 26px;
    margin-bottom: 8px;
  }

  .contact-subtitle {
    font-size: 15px;
    margin-bottom: 20px;
  }

  /* Contact rows: more compact */
  .connect-item {
    justify-content: center;
    align-items: center;
    grid-gap: 12px;
    gap: 12px;
    margin-bottom: 14px;
  }

  .icon-circle {
    width: 42px;
    height: 42px;
    font-size: 18px;
  }

  .connect-label {
    font-size: 0.95rem;
  }

  .connect-value {
    font-size: 0.9rem;
  }

  .map-btn {
    font-size: 15px;
    padding: 10px 16px;
    margin-top: 18px;
  }

  /* Testimonial & reach-out stack */
  .connect-testimonial-grid {
    flex-direction: column;
    align-items: center;
    grid-gap: 22px;
    gap: 22px;
    padding: 30px 16px 18px 16px;
  }

  .reach-out-section,
  .testimonial-end-card {
    width: 100%;
    min-width: 0;
    margin-bottom: 10px;
  }

  .testimonial-end-card {
    margin: 0;
  }
}

/* EXTRA SMALL PHONES */
@media (max-width: 480px) {
  .contact-heading-text,
  .blog-heading-text {
    font-size: 26px;
  }

  .contact-subtitle,
  .contact-header-detail-text,
  .blog-header-detail-text {
    font-size: 14px;
  }

  .contact-card {
    padding: 18px 14px;
  }
}

